.blog{
    display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr 3fr 1fr ;
  grid-column-gap: 10px;
}

.blog1{
    margin:0 ;
    padding: 3.5em 5em 0 5em;
    grid-column:2/6 ;
    grid-row: 1/2;
}
.blog1 p{
 padding-bottom: 2em ;   
}

.side-box-lt{
    margin:0 ;
    padding: 1em 0 2em 2em;
    grid-column:1/2 ;
    grid-row: 2/3; 
}
.side-box-wrapper_events{
    margin-bottom: 2em;
      
}
.side_box_eventslt{
    background: #fff;
    /* height: 27em;
    width: 16em; */
    height: 22em;
    width: 14em;
    padding-top: 0;
    /* border-radius: 5px;  */
    box-shadow: -1px 2px 34px -13px rgba(0,0,0,0.75);
    text-align: left;
    overflow: hidden;
    margin:.5em 10px 0 0; 
    margin:.5em 0px 0 12px;
   
}

.side_box_title{
    color: #273859;
    font-size: 1.7rem;
    font-weight: 600;
    border-bottom: .5px solid #d6dadd ;
    padding:1em ;

}
.side_box_subtitle {
    padding:0 2em;
}

.side-box-rt{
    margin:0 ;
    padding: 0em 5em 2em 4em;
    grid-column:2/6 ;
    grid-row: 2/3; 

}
.process_side_box {
    height:14.9em;
   
      background:white;
              
      overflow: hidden;
      margin-top: 2em;
           
    }
    .side_box_eventsrt{
    
        background: #fff;
        box-shadow: 3px 15px 8px  rgb(231, 236, 247);
        text-align: left;
        overflow: hidden;
        margin:.5em 10px 0 0; 
        margin:.5em 0px 0 12px;

    }
    .events-sm-image{
        width:14em;
        height:8em;
       float:left;
       /* border:1px solid #46608a; */
       border-radius:2px;
       margin:1.5em;
       padding:0;
       transition: all ;
            
    }
    .events_head{
        text-align: left;
        margin:1.5em 1.5em 1em 1.5em ;
  
      }
      .events_para{
        padding-left:1.5em;
        padding-right:1.5em;
         margin-bottom:2em;   
     }