* {
  margin: 0 ;
  padding: 0;
  box-sizing: border-box;
  font-family: Roboto, Open Sans, PT Sans;
}
:root {
  --primaryColor: #af9a7d;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --navColor: #273859;
  --secColor:#A0F2EA;
  --mainBlack: #222;
  --paraBlack:rgb(0, 0, 0);
  --mainGrey: #ececec;
  --darkGrey: #cfcfcf;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 3px;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}
/* globals */
body {
  /* padding-top: 66px; */
  /* color: var(--mainBlack);
  background: var(--mainWhite);
  
  line-height: 1.4; */
}
h1 {
  font-size: 3em;
  line-height: 1;
  margin-bottom: 0.5em;
}
h2 {
  /* font-size: 2em;
  margin-bottom: 0.75em; */
  font-size: 38px;
  margin-bottom: 20px;
}
h3 {
  font-size: 1.5em;
  line-height: 1;
  margin-bottom: 1em;
}
h4 {
  font-size: 1.2em;
  line-height: 1.25;
  margin-bottom: 1.25em;
}
h5 {
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 1.5em;
}
h6 {
  font-size: 1em;
  font-weight: bold;
  /* margin-bottom: 1.5em; */
  margin-bottom: 1em;
}
p{
  font-family: Roboto,Open Sans, PT Sans;
  font-size: 16px; 
  line-height: 150%;
  color: var(--paraBlack);
  justify-content: space-around;
  font-weight: normal;
  text-align: left;

}


.btn-primary {
  display: inline-block;
  text-decoration: none;
  letter-spacing: var(--mainSpacing);
  color: var(--mainBlack);
  background: var(--primaryColor);
  padding: 0.4rem 0.9rem;
  border: 3px solid var(--primaryColor);
  transition: var(--mainTransition);
  text-transform: uppercase;
  cursor: pointer;
}
.btn-primary:hover {
  background: transparent;
  color: var(--primaryColor);
}
.loading {
  text-transform: capitalize;
  text-align: center;
  margin-top: 3rem;
}
.error {
  text-align: center;
  text-transform: uppercase;
  margin: 2rem 0;
}
.empty-search {
  text-align: center;
  text-transform: capitalize;
  margin: 2rem 0;
  padding: 1rem;
  letter-spacing: var(--mainSpacing);
}
/* end of globals */
/* Navbar */
.NavbarItems {
  height:60px;
  display:flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
   /* background: linear-gradient(90deg,rgb(110,94,254) 0%, rgba(73,63,252,1) 100%); */
   background:var(--navColor);
   
}
.navbar-logo{
  color: var(--mainWhite);
  justify-self:start;
  margin-left: 20px;
  cursor: pointer;

}

 .nav-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.nav-icon {
  font-size: 1.5rem;
  margin-left: .5em;
  color: var(--mainWhite);
}
.nav-menu{
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 10px;
  list-style: none;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}
.nav-links{
  color: var(--mainWhite);
  text-decoration: none;
  padding: .5rem 1rem;

}
.nav-links:hover{
  background-color: #2983A6;
  border-radius: 4px;
  transition: all .2s ease-out;
}
.nav-links-mobile{
  display: none;
}
.menu-icon{
  display: none;
  /* color: var(--mainWhite); */
}
/* end of Navbar */

/* Homepage */
.homepage{
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows:auto 1fr 1fr 1fr ;
  grid-column-gap: 10px;
  padding-bottom: 3em;
 
  /* grid-template-columns: minmax(1.25em, 2fr) 4fr minmax(1.25em, 2fr);
  */
}
.banner{
grid-column: 1/7;
}
.about{
  /* background-color: var(--offWhite) ; */
  margin:0 ;
  padding: 2em 5em 0 5em;
  grid-column:2/6 ;
  grid-row: 2/3;
}
.mainh3{
  padding-top: .75em;
}
.line{
  width: 100%;
  height: 0;
  margin:3em 0 3em 0;
  padding: 0 5em;
  border-top: .1px solid #cfcfcf;
    
}
.visionpara{
  grid-column:2/6 ;
  grid-row: 3/4;
  padding:2em 5em 0 5em;
  display:flex;
  justify-content: space-between;
}
.vision{ 
  /* grid-column: 3 /span 2 ; */
  flex-basis: 50%;

}
.domain{
  grid-column:2/6 ;
  grid-row: 4/5;
  padding:0 5em;
 
}
.services{
  display:flex;
  
  justify-content: space-between;
 
}
.service{
  
  flex-basis: 30%;
}
.service h5{
 
}
.team{
  grid-column:2/6 ;
  grid-row: 5/6;
  padding:0 5em;
 

}
.teammembers{
  display:flex;
  
  justify-content: space-between;
}



/* end of Homepage */
/* About */
.aboutus{
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr 1fr 1fr  ;
  grid-column-gap: 10px;
  padding-bottom: 2.5em;

}

.about1{
  margin:0 ;
  padding: 2em 5em 0 5em;
  grid-column:2/6 ;
  grid-row: 1/2;
}

.about2{
  grid-column:2/6 ;
  grid-row: 2/4;
  padding:2em 5em 0 5em;
 
}
.about21{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;

}
.about211{
  flex-basis: 50%;
  padding:1em 2em 1em 0;
 
}
/* end of About */
/* services */
.analyticservice{
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr  ;
  grid-column-gap: 10px;
  padding-bottom: 2.5em;
}
.services2{
  margin:0 ;
  padding: 2em 5em 0 5em;
  grid-column:2/6 ;
  grid-row: 1/3; 
}
.services21{
 
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.services212{
  padding:1em 2em 1em 0;
  flex-basis: 50%;
}


/* end of services */


/* solutions */
.analyticsolution{
  padding-top: 2em;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 2fr 1fr ;
  grid-column-gap: 10px;
  margin-bottom: 4.3em;
}
.solutions1{
  grid-column:2/6 ;
  grid-row: 1/3;
  padding:0 5em;
}
.solutions12{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;

}
.solutions121{
  padding:1em 2em 1em 0;
 flex-basis: 50% ;
}

/* end of solutions */

/*Footer */
.footer{
  background-color: var(--secColor) ;
  /* color:var(--secColor); */
  height: 12em;
  position: relative;
  overflow: hidden;
  display:flex;
  flex-wrap: wrap;
  justify-content: space-around;

}
.footerpara{
  padding: 2em 5em 0 5em;
  flex-basis: 30% ;

}
.footerpara p{
  font-size: 14px;
  font-weight: bold;
  padding-bottom: .5em;
  color: var(--mainBlack);
}
.footerpara link{
  color: var(--mainBlack);
}




/* end of Footer */



@media screen and (max-width : 960px){
  .NavbarItems{
    position: relative;
  }
  .nav-menu{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 500px;
    position: absolute;
    top:80px;
    left:-100%;
    opacity: 1;
    transition: all .5s ease;
  }
  .nav-menu.active{
    background: var(--navColor);
    left: 0;
    opacity: 1;
    transition: all .5s ease;
    z-index: 1;

  }
  .nav-links{
    text-align: center;
    padding: 1rem;
    font-size: 1em;
    font-weight: 500; 
    margin: 0;
    width: 100%;
    display: table;
  }
  .nav-links:hover{
    background-color: #2983A6;
    border-radius: 0;

  }
  .navbar-logo{
    position: absolute;
    top: 0;
    left: 0;transform: translate(25%, 50%);

  }
  .menu-icon{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;

  }
}

